				content: "";
				width: .45em;
				height: .45em;
				border-bottom: .063em solid $color;
				border-left: .063em solid $color;
				position: absolute;
				right: 0.8em;
				top: 0.9em;
				transform: rotate(-45deg);
				cursor: pointer; }

			.form__book {
				p {
					display: block;
					margin: 0;
					color: $color;
					font-size: 1em;
					font-weight: 300;
					margin-bottom: 1.2em;
					margin-top: .6em; }

				input[type="radio"] {
					display: none; }

				section {
					margin-top: 0.7em;

					input {
						margin-bottom: 0; }

					input:disabled {
						display: none; } }

				.form__radio-button-email:checked ~ nav .form__label-email span:after,
				.form__radio-button-tel:checked ~ nav .form__label-tel span:after {
					content: "";
					display: block;
					width: 0.2em;
					height: 0.6em;
					border-right: .07em solid  $color;
					border-bottom: .07em solid $color;
					transform: rotate(40deg); }

				nav {
					display: flex;

					label {
						font-size: .875em;
						font-weight: 300;
						cursor: pointer;
						color: $color;
						display: flex;
						flex-direction: row-reverse;
						align-items: center;

						span {
							display: flex;
							align-items: center;
							justify-content: center;
							width: .8em;
							height: .8em;
							border: .063em solid $borderColor;
							margin-right: 1.3em;
							overflow: hidden; } }

					.form__label-tel {
						margin-left: 4.8em; } } }

			input[type="submit"] {
				margin: 0 auto;
				display: block;
				color: #fff;
				font-family: Roboto;
				font-size: 1em;
				font-weight: 200;
				width: 100%;
				min-height: 2.71em;
				background: linear-gradient(to top, $gradientTo 0, $gradientFrom 100%);
				border: .063em solid $color;
				border-radius: .6em;
				cursor: pointer;
				margin-top: 2.5em;
				-webkit-appearance: none; }

			input[type="submit"]:hover {
				background: #fff;
				color: $color;
				transition: .3s; } } }
	/* Style #{$i} Preset 6 End */ }
